import React from "react";
import Jhon_Cover_Pic from "../../../Assets/img/JhonInalienFullPicSmall8.png";

import { useContentfulData } from "../../Content/useContentfulData";
import "../../../Assets/scss/loading.scss";

import LoadingData from "../../../Assets/Loading.jsx";

import Jhon_Cover_Pic_Mobile from "../../../Assets/img/JhonInalienHalfPicSmall8.png";
import "./Cover.scss";
import "./CoverSm.scss";
import "./CoverMd.scss";

import Grid from "@material-ui/core/Grid";
import { Typography } from "@material-ui/core";

const Cover = () => {
  const [aboutData, loadingAboutData] = useContentfulData("about");

  if (loadingAboutData) {
    return <LoadingData />;
  }

  return (
    <div
      className="cover-section container .MuiGrid-align-items-xs-center"
      id="cover"
    >
      {aboutData.items.map((about) => {
        return (
          <Grid
            container
            spacing={1}
            direction="row"
            justify="space-evenly"
            alignItems="center"
            className="cover-height"
          >
            <Grid className="" item xs={12} sm={6}>
              <div className="cover-image-mobile">
                <img src={Jhon_Cover_Pic_Mobile} alt="Jhon Inalien" />
              </div>

              <p className="cover-heading">Hello</p>
              <Typography variant="h1" className="cover-name">
                I'm{" "}
                <span className="primary-color">
                  {about.fields.aboutFirstName}
                </span>{" "}
                {about.fields.aboutLastName}
              </Typography>
              <Typography className="cover-title mobile-only">
                {about.fields.aboutTitle} {"/"}{" "}
                {"Software QA Automation Engineer"}
              </Typography>

              <Typography variant="body1" className="cover-text">
                {about.fields.aboutBio}
              </Typography>
              <div style={{ paddingTop: "2em" }}>
                <Typography variant="subtitle1" className="tagline-text">
                  {about.fields.aboutTagLine}
                </Typography>
              </div>

              <div className="text-left" style={{ paddingTop: "3em" }}>
                <a
                  href="https://docs.google.com/document/d/129VU3fbP6z5hp8_O2fmfg_pHpnWvEELbznomwKbXmPI/edit?usp=sharing"
                  className="btn-default"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  See Resume
                </a>
              </div>
            </Grid>
            <Grid item xs={12} sm={4} md={4} lg={6}>
              <img
                className="cover-image "
                src={Jhon_Cover_Pic}
                alt="Jhon Inalien"
              />
            </Grid>
          </Grid>
        );
      })}
    </div>
  );
};
export default Cover;
